import React, { Component } from "react";
import AnnouncementDataService from "../../services/announcement.service";
import AnnouncementData from "../../types/announcement.type";
import Snackbar from "@mui/material/Snackbar";
import IconButton from "@mui/material/IconButton";
import ApplicationDataService from "../../services/applications.service";
import AuthService from "../../services/auth.service";

type Props = {};
type State = {
  announcements: Array<AnnouncementData>;
  announcementSelected: string;
  currentAnnouncement: AnnouncementData;
  open: boolean;
  message: string;
};

type FormElement = React.FormEvent<HTMLFormElement>;
type ChangeElement = React.ChangeEvent<HTMLInputElement>;

export default class ApplyToAnnouncement extends Component<Props, State> {
  constructor(props: Props) {
    super(props);

    this.state = {
      announcements: [],
      announcementSelected: "",
      currentAnnouncement: {
        id: "",
        titulo: "",
        consultorEnc: "",
        codigo: "",
        descripcion: "",
        fechaLimRec: "",
        fechaIniDur: "",
        fechaFinDur: "",
        documento: "",
        publica: false,
        pliego: "",
      },

      open: false,
      message: "",
    };

    this.retrieveAnnouncements = this.retrieveAnnouncements.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  componentDidMount() {
    this.retrieveAnnouncements();
  }

  retrieveAnnouncements() {
    AnnouncementDataService.get(AuthService.getCurrentUser().consultor_id)
      .then((response) => {
        this.setState({
          announcements: response.data.convocatorias,
        });
        console.log(response.data);
      })
      .catch((e) => {
        console.log(e);
      });
  }

  async handleSubmit(e: FormElement) {
    e.preventDefault();
    if (this.state.announcementSelected === "") {
      this.setState({
        message: "No existe ninguna convocatoria seleccinada",
        open: true,
      });
      return;
    }
    const selectAnnouncement = await this.state.announcements.filter(
      (announcement: AnnouncementData) =>
        announcement.id == this.state.announcementSelected
    )[0];
    this.setState({
      currentAnnouncement: selectAnnouncement,
    });
  }

  render() {
    const closeSnackbar = (
      event: React.SyntheticEvent | React.MouseEvent,
      reason?: string
    ) => {
      if (reason === "clickaway") {
        return;
      }
      this.setState({ open: false });
    };

    const actionCloseSnackbar = (
      <IconButton
        size="small"
        aria-label="close"
        color="inherit"
        onClick={closeSnackbar}
      >
        <span aria-hidden="true">&times;</span>
      </IconButton>
    );

    return (
      <>
        <div
          className="modal fade"
          id="confirmApplyModal"
          tabIndex={-1}
          aria-hidden={true}
        >
          <div className="modal-dialog">
            <div className="modal-content">
              <div className="modal-header">
                <h5 className="modal-title">
                  ¿Usted como grupo-empresa GRUPOEMPRESA desea postular a la
                  convocatoria "{this.state.currentAnnouncement.titulo}"?
                </h5>
                <button
                  type="button"
                  className="btn-close"
                  data-bs-dismiss="modal"
                  aria-label="Close"
                ></button>
              </div>
              <div className="modal-footer">
                <button
                  type="button"
                  className="btn btn-secondary"
                  data-bs-dismiss="modal"
                  onClick={() => {
                    window.location.reload();
                  }}
                >
                  Cancelar
                </button>
                <button
                  type="button"
                  className="btn btn-primary"
                  data-bs-dismiss="modal"
                  onClick={() => {
                    if (
                      new Date() >
                      new Date(this.state.currentAnnouncement.fechaLimRec)
                    ) {
                      this.setState({
                        message:
                          "Aplicación fuera de rango de fechas de postulación",
                        open: true,
                      });
                    } else {
                      ApplicationDataService.createApply(
                        {
                          convocatoria_id: this.state.currentAnnouncement.id,
                        },
                        AuthService.getCurrentUser().id
                      );
                      this.setState({
                        message:
                          "Usted GRUPOEMPRESA ha aplicado correctamente, puede proceder a llenar los documentos de la postulación.",
                        open: true,
                      });
                      setTimeout(() => {
                        window.location.reload();
                      }, 3000);
                    }
                  }}
                >
                  Aceptar
                </button>
              </div>
            </div>
          </div>
        </div>
        <div className="container p-3 position-relative">
          <div className="row">
            <div className="col-8">
              <h3> Aplicar a una convocatoria</h3>
            </div>
          </div>
          <form onSubmit={this.handleSubmit}>
            <div className="form-group row ps-4 pt-3">
              <select
                className="form-select form-select-lg mb-3"
                value={this.state.announcementSelected}
                onChange={(e) => {
                  this.setState({ announcementSelected: e.target.value });
                }}
              >
                <option value="" selected>
                  Seleccione la convocatoria a la que desea aplicar
                </option>
                {this.state.announcements.map(
                  (announcement: AnnouncementData) => (
                    <option value={announcement.id}>
                      {announcement.titulo} | {announcement.codigo}
                    </option>
                  )
                )}
              </select>
            </div>
            <div className="form-group row mt-5 d-flex justify-content-end">
              <div className="col-auto">
                <button
                  type="button"
                  className="btn btn-danger text-white"
                  onClick={() => {
                    window.location.reload();
                  }}
                >
                  Cancelar
                </button>
                <button
                  type="submit"
                  className="btn btn-success ms-3"
                  data-bs-toggle={
                    this.state.announcementSelected !== "" ? "modal" : ""
                  }
                  data-bs-target={
                    this.state.announcementSelected !== ""
                      ? "#confirmApplyModal"
                      : "#"
                  }
                >
                  Aplicar
                </button>
              </div>
            </div>
          </form>
        </div>
        <Snackbar
          open={this.state.open}
          autoHideDuration={6000}
          onClose={closeSnackbar}
          message={this.state.message}
          action={actionCloseSnackbar}
        />
      </>
    );
  }
}
